import { useState } from "react";
import { Link as RouterLink, useNavigate } from "react-router-dom";
import { format } from "date-fns";
import numeral from "numeral";
import PropTypes from "prop-types";
import {
  Box,
  Button,
  Card,
  Checkbox,
  IconButton,
  InputAdornment,
  Link,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TextField,
  Typography,
} from "@material-ui/core";
import ArrowRightIcon from "../../icons/ArrowRight";
import PencilAltIcon from "../../icons/PencilAlt";
import SearchIcon from "../../icons/Search";
// import Label from "../../Label";
import axios from "axios";
import Scrollbar from "../../components/Scrollbar";
import toast from "react-hot-toast";
import { customAxios } from "../apis/Axios";

const statusOptions = [
  {
    label: "All",
    value: "all",
  },
  {
    label: "theory",
    value: "theory",
  },
  {
    label: "practicle",
    value: "practicle",
  },
];

const sortOptions = [
  {
    label: "Newest first",
    value: "createdAt|desc",
  },
  {
    label: "Oldest first",
    value: "createdAt|asc",
  },
];

const applyFilters = (invoices, query, filters) =>
  invoices.filter((invoice) => {
    let matches = true;
    //   console.log(invoice)
    if (query) {
      const properties = ["subject"];
      let containsQuery = false;

      properties.forEach((property) => {
        if (invoice[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    if (filters.subjectType && invoice.subjectType !== filters.subjectType) {
      matches = false;
    }

    return matches;
  });

const applyPagination = (invoices, page, limit) =>
  invoices.slice(page * limit, page * limit + limit);

const InvoiceListTable = (props) => {
  const { invoices, setRefresh, refresh, ...other } = props;

  const Navigate = useNavigate();
  const [selectedInvoices, setSelectedInvoices] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState("");
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    subjectType: null,
  });

  const handleQueryChange = (event) => {
    setQuery(event.target.value);
  };

  const handleStatusChange = (event) => {
    let value = null;

    if (event.target.value !== "all") {
      value = event.target.value;
    }

    setFilters((prevFilters) => ({
      ...prevFilters,
      subjectType: value,
    }));
  };

  const handleSortChange = (event) => {
    setSort(event.target.value);
  };

  const handleSelectAllInvoices = (event) => {
    setSelectedInvoices(
      event.target.checked ? invoices.map((invoice) => invoice.id) : []
    );
  };

  const handleSelectOneInvoice = (event, invoiceId) => {
    if (!selectedInvoices.includes(invoiceId)) {
      setSelectedInvoices((prevSelected) => [...prevSelected, invoiceId]);
    } else {
      setSelectedInvoices((prevSelected) =>
        prevSelected.filter((id) => id !== invoiceId)
      );
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value, 10));
  };
  const handleDelete = (id) => {
    // console.log(selectedInvoices);
    try {
      // if (confirm("Are Your Sure You Want To Delete") == true) {
      //   let idString = "";
      //   selectedInvoices.forEach((ele) => {
      //     idString = ele + "," + idString;
      //     // console.log(idString);
      //   });
      //   idString = idString.slice(0, -1);
      //   console.log(idString);
      //   const obj = {
      //     id: idString,
      //   };

      //   axios
      //     .delete(`http://localhost:8000/books/del/subject`, { data: obj })
      //     .then((res) => {
      //       // console.log(res)
      //       res.data.json;
      //       setSelectedInvoices([]);
      //       setRefresh(!refresh);

      //       // ("res",res)
      //     });
      // } else {
      //   setSelectedInvoices([...selectedInvoices]);
      // }
      const confiramtion = window.confirm("Are Your Sure You want To Delete");
      try {
        if (confiramtion) {
          customAxios.deleteCall(`books/del/subject/${id}`)
            .then((res) => {
              toast.success(res.message);
              setRefresh(!refresh);
              setSelectedInvoices([]);
            });
        } else {
          setSelectedInvoices([...selectedInvoices]);
        }
      } catch (err) {
        if (err) throw err;
      }
    } catch (err) {
      console.log(err);
    }
  };
  // Usually query is done on backend with indexing solutions
  const filteredInvoices = applyFilters(invoices, query, filters);
  const paginatedInvoices = applyPagination(filteredInvoices, page, limit);
  // const enableBulkActions = selectedInvoices.length > 0;
  const selectedSomeInvoices =
    selectedInvoices.length > 0 && selectedInvoices.length < invoices.length;
  const selectedAllInvoices = selectedInvoices.length === invoices.length;

  return (
    <Card {...other}>
      <Box
        sx={{
          alignItems: "center",
          display: "flex",
          flexWrap: "wrap",
          m: -1,
          p: 2,
        }}
      >
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 500,
          }}
        >
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon fontSize="small" />
                </InputAdornment>
              ),
            }}
            onChange={handleQueryChange}
            placeholder="Search class by Class Name"
            value={query}
            variant="outlined"
          />
        </Box>
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 240,
          }}
        >
          <TextField
            fullWidth
            label="Sort By"
            name="sort"
            onChange={handleSortChange}
            select
            SelectProps={{ native: true }}
            value={sort}
            variant="outlined"
          >
            {sortOptions.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </TextField>
        </Box>
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 240,
          }}
        >
          <TextField
            fullWidth
            label="Subject Type"
            name="subjectType"
            onChange={handleStatusChange}
            select
            SelectProps={{ native: true }}
            value={filters.subjectType || "all"}
            variant="outlined"
          >
            {statusOptions.map((statusOption) => (
              <option key={statusOption.value} value={statusOption.value}>
                {statusOption.label}
              </option>
            ))}
          </TextField>
        </Box>
      </Box>
      {/* {enableBulkActions && (
        <Box sx={{ position: "relative" }}>
          <Box
            sx={{
              backgroundColor: "background.paper",
              mt: "6px",
              position: "absolute",
              px: "4px",
              width: "100%",
              zIndex: 2,
            }}
          >
            <Checkbox
              checked={selectedAllInvoices}
              color="primary"
              indeterminate={selectedSomeInvoices}
              onChange={handleSelectAllInvoices}
            />
            <Button
              onClick={handleDelete}
              color="primary"
              sx={{ ml: 2 }}
              variant="outlined"
            >
              Delete
            </Button>
            <Button color="primary" sx={{ ml: 2 }} variant="outlined">
              Edit
            </Button>
          </Box>
        </Box>
      )} */}
      <Scrollbar>
        <Box sx={{ minWidth: 1200 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllInvoices}
                    color="primary"
                    indeterminate={selectedSomeInvoices}
                    onChange={handleSelectAllInvoices}
                  />
                </TableCell>
                <TableCell>Sr.No</TableCell>
                <TableCell>Subject</TableCell>
                <TableCell>Subject Type</TableCell>
                <TableCell>Created</TableCell>
                <TableCell>Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedInvoices.map((invoice, index) => {
                // console.log(invoice,index)
                // console.log("inv",invoice)
                const isInvoiceSelected = selectedInvoices.includes(invoice.id);

                return (
                  <TableRow hover key={invoice.id} selected={isInvoiceSelected}>
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isInvoiceSelected}
                        color="primary"
                        onChange={(event) =>
                          handleSelectOneInvoice(event, invoice.id)
                        }
                        value={isInvoiceSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Link
                        color="textPrimary"
                        component={RouterLink}
                        to="#"
                        underline="none"
                        variant="subtitle2"
                      >
                        {index + 1}
                      </Link>
                    </TableCell>
                    <TableCell>{invoice.subjectName}</TableCell>

                    {/* <TableCell>
                      {numeral(invoice.totalAmount).format(
                        `${invoice.currency}0,0.00`
                      )}
                    </TableCell> */}
                    <TableCell>{invoice.subjectType}</TableCell>
                    <TableCell>{invoice.subjectType}</TableCell>

                    {/* <TableCell>
                      {format(invoice.issueDate, "dd/MM/yyyy")}
                    </TableCell> */}
                    <TableCell>
                      {/* <IconButton component={RouterLink} to="#"> */}
                      <Button
                        style={{ marginRight: 5 }}
                        size="small"
                        onClick={() =>
                          Navigate("/dashboard/add_subject", {
                            state: { invoice },
                          })
                        }
                        variant="contained"
                        color="primary"
                      >
                        edit
                      </Button>
                    
                      <Button
                        onClick={(e) => {
                          handleDelete(invoice.id, e);
                        }}
                        size="small"
                        variant="contained"
                        color="error"
                      >
                        Delete
                      </Button>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </Scrollbar>
      <TablePagination
        component="div"
        count={filteredInvoices.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

InvoiceListTable.propTypes = {
  invoices: PropTypes.array.isRequired,
};

export default InvoiceListTable;
